.UaAdministrateur-LosangePhotoPole{
    transform: rotate(-45deg);
    width: 150px;
    height: 150px;
    overflow: hidden;
    display: grid;
    grid-template-columns: 10px 130px 10px;
}

.photoAdministrateur{
    margin-left: -35px;
    margin-top: -26px;
    width: 200px;
    height: 200px;
    transform: rotate(45deg);
}

.UaAdministrateur-texte{
    font-family: "Poiret One", serif;
    font-weight: lighter;
    color: #13435b;
    font-size: 20px;
    width: 200px;
    margin: 5px;
    display: grid;
}

.UaAdministrateurGauche-texte{text-align: right; grid-area:a}
.UaAdministrateurDroite-texte{text-align: left; grid-area:b}

.UaAdministrateurGauche-Container, .UaAdministrateurDroite-Container{
    display: grid;
    grid-template-areas: "a b";
    grid-gap: 50px;
    justify-content: center;
    align-items: center;
    padding: 20px;
}
.UaAdministrateurGauche-Container{margin-right: -45px;}
.UaAdministrateurDroite-Container{margin-left: -45px;}

